html {
  font-size: 10px; }
body {
  background: #FFF;
  // +css-lock(16, 30, 600, 1280)
  line-height: 1.4;
  font-family: regular;
  margin-top: 80px;
  @include r(1300) {
    margin-top: 109px; }
  @include r(700) {
    margin-top: 71px; }
  @include miw(320); }

::selection {
  background: #ccc; }
::-moz-selection {
  background: #ccc; }
::-webkit-selection {
  background: #ccc;
  color:#fff {} }

a {
  text-decoration: none;
  color: #000; }

.container {
  @include maw(1280);
  margin: 0 auto;
  @include r(1300) {
    // +maw(1024)
    padding-left: 32px;
    padding-right: 32px; }
  @include r(1024) {
    padding-left: 24px;
    padding-right: 24px; }
  @include r(375) {
    padding-left: 20px;
    padding-right: 20px; } }
